/*
 * Validation funcs
 */

function makeMatchesFunction(partialMatch) {
  return function matches(a) {
    if (!multiaddr.isMultiaddr(a)) {
      try {
        a = multiaddr(a);
      } catch (err) {
        // catch error
        return false; // also if it's invalid it's propably not matching as well so return false
      }
    }

    var out = partialMatch(a.protoNames());

    if (out === null) {
      return false;
    }

    return out.length === 0;
  };
}

function and() {
  var args = Array.from(arguments);

  function partialMatch(a) {
    if (a.length < args.length) {
      return null;
    }

    args.some(function (arg) {
      a = typeof arg === 'function' ? arg().partialMatch(a) : arg.partialMatch(a);

      if (a === null) {
        return true;
      }
    });
    return a;
  }

  return {
    toString: function toString() {
      return '{ ' + args.join(' ') + ' }';
    },
    input: args,
    matches: makeMatchesFunction(partialMatch),
    partialMatch: partialMatch
  };
}

function or() {
  var args = Array.from(arguments);

  function partialMatch(a) {
    var out = null;
    args.some(function (arg) {
      var res = typeof arg === 'function' ? arg().partialMatch(a) : arg.partialMatch(a);

      if (res) {
        out = res;
        return true;
      }
    });
    return out;
  }

  var result = {
    toString: function toString() {
      return '{ ' + args.join(' ') + ' }';
    },
    input: args,
    matches: makeMatchesFunction(partialMatch),
    partialMatch: partialMatch
  };
  return result;
}

function base(n) {
  var name = n;

  function matches(a) {
    if (typeof a === 'string') {
      try {
        a = multiaddr(a);
      } catch (err) {
        // catch error
        return false; // also if it's invalid it's propably not matching as well so return false
      }
    }

    var pnames = a.protoNames();

    if (pnames.length === 1 && pnames[0] === name) {
      return true;
    }

    return false;
  }

  function partialMatch(protos) {
    if (protos.length === 0) {
      return null;
    }

    if (protos[0] === name) {
      return protos.slice(1);
    }

    return null;
  }

  return {
    toString: function toString() {
      return name;
    },
    matches: matches,
    partialMatch: partialMatch
  };
}